export default {
	data() {
		return {
			selected: 0,
			show: true,
			users: {},
			input_keyWords: '',
			friends: []
		};
	},
	methods: {
		selectAll: function() {
			uni.request({
				url: this.$baseUrl + '/friend/keyword',
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				data: {
				fromId: this.users.id,
					keyWords: ''
				},
				success: res => {
					this.friends = res.data.data;
					if (res.data.code === 1) {
						console.log(res.data.data);
					} else {
						uni.showToast({
							title: res.data.msg
						});
					}
				}
			});
		},
		selectFriend: function() {
			uni.request({
				url: this.$baseUrl + '/friend/keyword',
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				data: {
					fromId: this.users.id,
					keyWords: this.input_keyWords
				},
				success: res => {
					if (res.data.code === 1) {
						console.log(res.data.data);
						this.friends = res.data.data;
					}
				}
			});
		},
		deleteFend:function(fromId,toId){
			uni.request({
				url:this.$baseUrl+'/friend/friend',
				method:'DELETE',
				data:{
					fromId:fromId,
					toId:toId,
				},
				success : res =>{
					if(res.data.code ===1){
						uni.showToast({
							title: res.data.msg
						});
					}else{
						uni.showToast({
							title: res.data.msg
						});
					}
				}
			})
		}
	},
	onLoad(){
		// this.selectAll()
		let that = this;
		uni.getStorage({
			key: 'user',
			success: function(res) {
				that.users = res.data;
				console.log(that.users);
				that.selectAll();
			}
		});
	}
};
